* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

 body {
  
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
 
}

.app {
  background-image: url('./assets/windy.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}

.app.warm {
  background-image: url('./assets/sunny.jpg');
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom,rgba(0,0,0,0.2),rbga(0,0,0,0.75));
  padding: 25px;
}

.flex{
  display:flex;
  flex-direction: row;
}

.search-box{
  width: 80%;
  margin: 0,0,75px;
  display: flex;
  flex-direction: row;
  padding-top: 30px;
  justify-content: right;
  align-items: baseline;
}

.search-box .search-bar{
  display: flex;
  width: 60%;
  padding: 25px;
  height: 50%;
  text-align: center;
  justify-content: center;
  align-items: center;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(225,225,225,0.5);
  border-radius: 16px 16px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0,0,0,0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4 ease;

  
}

.search-bar .search-box:focus{
  background-color:rgba(225,225,225,0.75) ;
}

.search-box .btn {
  background-color: sandybrown;
  padding: 10px;
 align-items: center;
   color: #fff;

}

.btn{
  padding-top: 30px;
  align-items: center;
  border-radius: 16px 16px 16px 16px;
  margin-top: -25px;
  border: none;
  margin-left: 5px;
}

.location-box .location{
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50 ,70, 0.5 );
}

.location-box .date{
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px,2px, rgba(50, 50 ,70, 0.5 );
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(225,225,225,0.75);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px, 6px, rgba(50, 50 ,70, 0.5 );
  text-align: center;
  box-shadow: 3px, 6px, rgba(0,0,0,0.2);
}

.weather-box .weather{
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px,3px,rgba(50, 50 ,70, 0.5 );
}
 
